.friend_list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}
.item {
  display: flex;
  gap: 10px;
  align-items: center;
  width: 135px;
}
.status {
  border-radius: 50%;
  width: 10px;
  height: 10px;
}
.online {
  background-color: green;
}
.ofline {
  background-color: red;
}
.avatar {
  border-radius: 5px;
}
.name {
  font-weight: bold;
}
